html {
  font-size: 10px;
  line-height: 1.15;
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  font-family: system-ui; // Only supported on Chromium-based browsers and Safari
  font-size: 1.4rem;
  line-height: inherit;
  color: $lcg-1;
  background-color: $mcg-4;
  overflow-wrap: break-word;

  @include zero-edges;
  min-width: $mobile-width;
  min-height: 100vh;

  @include column-flex;
  padding: 50px 0;

  .list__item {
    font-size: 1.5em;
    line-height: 1;
    margin-bottom: 20px;
    text-align: center;
  }

  .list__link {
    text-decoration: none;
    color: $lcg-1;

    &:hover,
    &:focus {
      color: $mcg-9;
    }
  }
}

.visually-hidden {
  @include visually-hidden;
}

.list {
  @include zero-edges;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  @include link-base-style;
}

.link,
button {
  cursor: pointer;
}

.container {
  @include centering-margin;
  width: 100%;
  max-width: 1461px;
  padding: 0 25px;

  @media (max-width: $pre-tablet-width) {
    padding: 0 17px;
  }
}

//
// tooltips
//

.tooltip {
  position: relative;

  .tooltiptext {
    font-size: 1em;
    line-height: 1em;
    text-align: center;
    color: $lcg-1;
    background-color: $dcg-11;

    @include centering-flex;
    visibility: hidden;
    position: absolute;
    top: 130%;
    left: 50%;
    transform: translateX(-50%);
    padding: 1em;
    border-radius: 5px;
    z-index: 1;

    // Fade in tooltip
    opacity: 0;
    transition: opacity 0.4s ease;

    &::after {
      // *** triangle variant 1 ***
      // content: "";
      // position: absolute;
      // top: 0;
      // left: 50%;
      // transform: translate(-50%, -85%);

      // width: 0;
      // height: 0;
      // border-bottom: 13px * 0.87 solid $dcg-11;
      // border-left: 13px * 0.5 solid transparent;
      // border-right: 13px * 0.5 solid transparent;
      // border-top-width: 0;

      // *** triangle variant 2 ***
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -90%);
      border-width: 7px;
      border-style: solid;
      border-color: transparent transparent $dcg-11 transparent;
    }
  }

  &:hover {
    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
}
