.tabs {
  width: 100%;
  margin-bottom: 2.5em;

  color: $lcg-1;
  background-color: transparent;

  border-radius: 5px;
  overflow: hidden;
}

.tabs__inner {
  position: relative;
  padding-top: 4em;
}

.tabs__controls {
  position: absolute;
  top: 0;

  display: flex;
  width: 100%;
  outline: none;
  list-style: none;

  overflow-x: auto;
  scroll-snap-type: x mandatory;


  @media (min-width: $desktop-width) {
    left: 2%;
  }

  @media (max-width: $pre-desktop-width) {
    left: 3.2%;
  }
}

.tabs__control {
  scroll-snap-align: start;
  scroll-snap-stop: always;

  &:last-child {
    a {
      border-right: none;
    }
  }
}

.tabs__anchor-link {
  font-size: 1em;
  line-height: inherit;
  font-weight: bold;
  color: inherit;

  width: max-content;
  padding: 1em 1.5em;
  border: 1px solid currentColor;
  border-bottom: none;
  border-left: none;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;

  &:hover,
  &:focus {
    span {
      border-bottom: 1px solid currentColor;;
    }
  }
}

.tabs__contents {
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;

  @media (min-width: $desktop-width) {
    border-top-right-radius: 5px;
  }
}

.tabs__content {
  display: none;
}

.tabs__content.active {
  display: block;
  position: relative;
}

.tabs__arrow-box {
  position: absolute;
  top: 0;
  left: 100%;
  display: flex;
  width: max-content;
  margin-top: 10px;
  transform: translateX(-100%);
  z-index: 1;
}

.tabs__arrow {
  width: min-content;
  height: min-content;
  border: 1px solid transparent;
  border-radius: 5px;

  transform: scale(0.9);

  &--tostart {
    width: max-content;
    height: min-content;
    margin-top: 25px;
    border: none;
    border-bottom: 1px solid transparent;
    color: inherit;

    // &:hover,
    // &:focus {
    //   border-color: $lcg-1;
    // }

    // &:active {
    //   opacity: 0.7;
    // }
  }

  &:hover,
  &:focus {
    border-color: $lcg-1;
  }

  &:active {
    opacity: 0.7;
  }
}

.tabs__arrow-svg {
  --arrow-bg: transparent;
  --arrow-stroke: #{$lcg-1};

  // @media (min-width: $desktop-width) {
  //   width: 17.027027%;
  //   height: 10.1612903%;
  // }

  // @media (max-width: $pre-desktop-width) {
  //   width: 8.8112%;
  //   height: 24.609375%;
  //   transform: translate(-100%, -100%);
  // }

  // @media (max-width: $pre-tablet-width) {
  //   width: 63px;
  //   height: 63px;
  // }

  .background {
    fill: --arrow-bg;
  }

  .stroke {
    stroke: --arrow-stroke;
  }
}

.tabs__section {
  padding-top: 40px;

  h3 {
    margin-top: 0;
    margin-bottom: 40px;
  }

  p {
    text-align: justify;
  }
}
